export function getFile(url, type = 1) {
  if (!url) {
    return [];
  }
  let cur = [];
  let curNext = [];
  cur = curNext = (url && url.split("\u0001")) || [];
  if (!cur[cur.length - 1]) {
    cur.pop();
  }
  curNext = cur.map((item) => {
    return {
      name: fromUrlGetFileName(item),
      url: item,
    };
  });
  return curNext;
}
