export const pcgetOSType = () => {
  if (/macintosh|mac os x/i.test(navigator.userAgent)) {
    return "IOS";
  } else if (/windows|win32/i.test(navigator.userAgent)) {
    return "WINDOWS";
  } else {
    return "Other";
  }
};

export function generateRandomString() {
  let result = "";
  const characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  const charactersLength = characters.length;
  for (let i = 0; i < 32; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

export const logs = (op, plat, time) => {
  if (!op) return;
  console.log("op, plat, time: ", op, plat, time);
  // 999 页面事件点击量记录
  // 998 记录的XX停留时长
  plat = plat || 999;
  const _op = {
    "-2": "激活AI客服",
    "-1": "01你好",
    0: "01你好",
    1: "02了解企业概况",
    2: "03了解主营业务",
    3: "04商务联系",
    4: "05微信扫码关注",
    5: "06完成资料提交",
    7: "07打开AI对话",
    8: "08停留时长",
    9: "09白皮书入口",
    10: "10白皮书下载次数",
    11: "11完成白皮书下载",
    12: "12白皮书跳转",
    13: "13免责声明",
  }[op];

  const t_op = JSON.stringify({ name: _op, playTime: time });
  const c_op = time ? t_op : _op;
  // console.log("_op: ", _op, op);
  var xhr = new XMLHttpRequest();
  xhr.open(
    "GET",
    `https://dotlog.ymtech.info/dig.gif?${new URLSearchParams({
      t: new Date().getTime(),
      o: pcgetOSType(),
      op: c_op,
      p: plat,
      u: "tourist",
      d: "PC",
      s: 2,
      v: "1.0.0",
    })}`
  );
  xhr.send();
};

export function calcTimes(startT) {
  const endT = Date.now();
  return parseInt((endT - startT) / 1000);
}
