import { createRouter, createWebHashHistory } from "vue-router";
import routes from "./route";
import store from "../store";

const router = createRouter({
  history: createWebHashHistory(),
  linkActiveClass: "is-active",
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { top: 0 };
    }
  },
});

// 路由拦截
router.beforeEach((to, from, next) => {
  console.log("路由拦截to: ", to);
  // 清除遗留数据
  store.commit("mFilterUpdata", {});
  // 引用AI视频推广模块，进入推广页面
  if (to.path == "/aipublic") {
    localStorage.setItem("aipublic", 1);
    next();
  }

  // 外部KYC认证，不走以下这套逻辑
  if (to.path == "/identifyResult") next();
  else if (
    to.path == "/identify" ||
    to.path.indexOf("/RZ") > -1 ||
    to.path.indexOf("/TESTRZ") > -1
  ) {
    if (to.path == "/identify") {
      localStorage.setItem("kycCode", to.query.code);
      next("/");
    } else {
      // /TESTRZ是测试模板，跳到本地环境进行测试
      const _RZ = to.path.split("RZ");
      localStorage.setItem("kycCode", _RZ[1]);
      if (_RZ[0].includes("TEST"))
        window.location.href = "http://172.30.90.190:8082/#/RZ" + _RZ[1];
      else next("/");
    }
  } else if (to.meta && to.path) {
    next();
  } else {
    next("/404");
  }
});

router.afterEach(() => {});

export default router;

/*
 * 路由约定：
 * 1.公共页面没有name属性
 * 2.不需要权限控制的页面没有meta.code属性
 */
